import * as swal from 'sweetalert';

export class PageType {
  public static VALUE_EDIT_ITEMS: string = 'edit_items';
  public static VALUE_EDIT_FIELDS: string = 'edit_fields';
  public static VALUE_EDIT_SETTINGS: string = 'edit_settings';

  public static TEXT_EDIT_ITEMS: string = '';
  public static TEXT_EDIT_FIELDS: string = '';
  public static TEXT_EDIT_SETTINGS: string = '';
}

export function getUploadFilesProps(url: string, multi: boolean, accept: string, success: (fileUrl: string) => void, error: (errorMessage: string) => void, progress: () => void) {
  return {
    action: url,
    multiple: multi,
    dataType: 'json',
    accept: accept,
    maxFileSize: 5000000, // 5 MB
    withCredentials: true,
    onSuccess(fileUrl: string) {
      success(fileUrl);
    },
    onError(err: { errorMessage: string }) {
      error(err.errorMessage);
    },
    onProgress() {
      progress();
    }
  };
}

export class Swal {
  static tip(title: string, text?: string, isTimer?: boolean) {
    if (isTimer) {
      swal({
        title: title,
        text: text,
        timer: 2000,
        confirmButtonText: '',
        cancelButtonText: '',
        html: true
      });
    } else {
      swal({
        title: title,
        text: text,
        confirmButtonText: '',
        cancelButtonText: '',
        html: true
      });
    }
  }

  static success(title: string, callback?: () => void) {
    swal(
      {
        title: title,
        text: '',
        type: 'success',
        confirmButtonText: '',
        html: true
      },
      callback);
  }

  static successConfirm(title: string, confirm: (isConfirm: boolean) => void) {
    swal(
      {
        title: title,
        type: 'success',
        showCancelButton: true,
        confirmButtonText: '',
        cancelButtonText: '',
        html: true
      },
      confirm);
  }

  static error(errorMessage: string, callback?: () => void) {
    swal(
      {
        title: errorMessage,
        text: '',
        type: 'error',
        confirmButtonText: '',
        cancelButtonText: '',
        html: true
      },
      callback);
  }

  static warning(title: string, text?: string) {
    swal({
      title: title,
      text: text,
      type: 'warning',
      confirmButtonText: '',
      cancelButtonText: '',
      html: true
    });
  }

  static delete(title: string, text: string, confirm: (isConfirm: boolean) => void) {
    swal(
      {
        title: title,
        text: text,
        type: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#f36c60',
        confirmButtonText: '',
        cancelButtonText: '',
        closeOnConfirm: true,
        html: true
      },
      confirm);
  }

  static confirm(title: string, text: string, confirm: (isConfirm: boolean) => void) {
    swal(
      {
        title: title,
        text: text,
        type: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#f36c60',
        confirmButtonText: '',
        cancelButtonText: '',
        closeOnConfirm: true,
        html: true
      },
      confirm
    );
  }
}



